:root {
  --c-class-dk							: 196, 30, 58;
	--c-class-dh							: 163,	48,	201;
	--c-class-druid						: 255,	124,	10;
	--c-class-hunter					: 170,	211,	114;
	--c-class-mage						: 63,	199,	235;
	--c-class-monk						: 0,	255,	152;
	--c-class-paladin					: 244,	140,	186;
	--c-class-priest 					: 255,	255,	255;
	--c-class-rogue	 					: 255,	244,	104;
	--c-class-shaman	 				:	0,	112,	221;
	--c-class-warlock 				: 135,	136,	238;
	--c-class-warrior 				: 198,	155,	109;
}

//Variables
$font-size-base             : 1.6rem;
$font-weight-base           : 400;
$line-height-base           : 1.2;

$ff-sans-serif              : "Helvetica Neue", Helvetica, Arial, sans-serif;
$ff-serif                   : Georgia, "Times New Roman", Times, serif;
$ff-montserrat              : 'Montserrat', sans-serif;
$ff-gruppo                  : 'Gruppo', cursive;

$t-duration                 : 0.3s;

$shell-max-width            : 114rem;
$shell-gutter               : 1.5rem;

$c-black                    : #000;
$c-white                    : #fff;
$c-base                     : #055590;

$c-dark											: #242424;
$c-dark-2										: #282828;
$c-dark-3                   : #484848;
$c-menu-link								: #bbb;

$c-item-poor                : #9d9d9d;
$c-item-common              : #fff;
$c-item-uncommon            : #1eff00;
$c-item-rare                : #0070dd;
$c-item-epic                : #a335ee;
$c-item-legendary           : #ff8000;
$c-item-artifact            : #e6cc80;
$c-item-heirloom            : #00ccff;

$c-class-dk									: #C41E3A;
$c-class-dh									: #A330C9;
$c-class-druid							: #FF7C0A;
$c-class-hunter							: #AAD372;
$c-class-mage								: #3FC7EB;
$c-class-monk								: #00FF98;
$c-class-paladin						: #F48CBA;
$c-class-priest 						: #FFFFFF;
$c-class-rogue	 						: #FFF468;
$c-class-shaman	 						: #0070DD;
$c-class-warlock 						: #8788EE;
$c-class-warrior 						: #C69B6D;

$classes: (
	deathknight	: var(--c-class-dk),
	demonhunter	: var(--c-class-dh),
	druid				: var(--c-class-druid),
	hunter			: var(--c-class-hunter),
	mage				: var(--c-class-mage),
	monk				: var(--c-class-monk),
	paladin			: var(--c-class-paladin),
	priest			: var(--c-class-priest),
	rogue				: var(--c-class-rogue),
	shaman			: var(--c-class-shaman),
	warlock			: var(--c-class-warlock),
	warrior			: var(--c-class-warrior)
);

$responsive-ios             : 812px;

$breakpoints: (
	mobile-small: 374px,
	mobile-medium: 575px,
	mobile: 767px,
	admin-bar: 782px,
	tablet: 1023px,
	small-desktop: 1199px,
	large-desktop: 1399px
);
