/* ------------------------------------------------------------ *\
	Fields
\* ------------------------------------------------------------ */

.field {
	font-size: 14px;
	font-weight: 300;
	width: 100%;
	padding: 10px;
	appearance: none;
	border: solid 1px $c-dark;
	background-color: $c-dark-3;

	&:focus {
		border: solid 1px $c-class-warrior;
	}
}

.select .field {
	padding-right: 30px;
}

.select {
	position: relative;

	&::after {
		content: '';
		width: 8px;
		height: 8px;
		border-right: solid 1px $c-white;
		border-bottom: solid 1px $c-white;
		transform: translateY(-65%) rotate(45deg);
		transform-origin: center;
		position: absolute;
		top: 50%;
		right: 13px;
	}
}

