/* ------------------------------------------------------------ *\
  build-items
\* ------------------------------------------------------------ */

.build-items-container {
  columns: 2;

  /* Tablet Portrait */
  @media (max-width: 1023px) {
    columns: 1;
  }
}

.build-items {
  -webkit-column-break-inside: avoid;
  page-break-inside: avoid;
  break-inside: avoid;

  // border-radius: 10px;
  padding: 15px;
  background-color: rgba($c-dark-3, 0.4);
  box-shadow: 3px 3px 5px rgba($c-dark, 1);

  &:not(:last-child) {
    margin-bottom: 15px;
  }

  .build-items__head {
    display: flex;
    align-items: center;
  }

  .build-items__image {
    margin-right: 10px;
  }

  .build-items__title {
    font-size: 24px;
  }

  @each $class, $color in $classes {
    &[data-class="#{$class}"] .build-items__title {
      color: rgb(#{$color});
    }
  }
}

.build-item {
  display: flex;
  padding: 10px;
  position: relative;

  /* Tablet Portrait */
  @media (max-width: 1023px) {
    text-align: center;
    display: block;
  }

  &::after {
    content: attr(data-type);
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    font-size: 32px;
    font-weight: 600;
    opacity: 0.5;

    /* Tablet Portrait */
    @media (max-width: 1023px) {
      content: none;
    }
  }

  .build-item__anchor {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .build-item__image {
    margin-right: 10px;

    /* Tablet Portrait */
    @media (max-width: 1023px) {
      margin-bottom: 5px;
    }
  }

  .build-item__title {
    font-size: 18px;
  }

  .build-item__stats,
  .build-item__author {
    font-size: 13px;
  }

  &[data-type="F2P"] {
    background-color: rgba(limegreen, 0.1);
  }

  &[data-type="VET"] {
    background-color: rgba(yellow, 0.1);
  }

  & + & {
    border-top: solid 1px rgba($c-white, 0.3);
  }
}
