/* ------------------------------------------------------------ *\
  form-calculator
\* ------------------------------------------------------------ */

.form-calculator {
  label {
    max-width: 60%;
    flex: 0 0 60%;
    line-height: 1;
    text-align: right;
    padding-right: 5px;
    font-size: 15px;
    font-weight: 300;
  }

  select {
    padding-left: 32px;
  }

  .select span {
    width: 24px;
    height: 24px;
    display: block;
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    background-size: contain;
  }

  .form__controls {
    display: flex;
    align-items: center;
  }

  .form__row + .form__row {
    margin-top: 10px;
  }
}
